import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Grid, Paper, Divider, OutlinedInput, Button, Card } from '@material-ui/core';
import SchoolIcon from '@material-ui/icons/School';
import useStyles from '../../ViewTableStyles';
import { getProfileSubscription, getInvoice } from '../../../store/actions/SubscriptionActions';
import Cookies from 'js-cookie';
import GetAppIcon from '@material-ui/icons/GetApp';
import useUserRole from '../../../hooks/useUserRole';

export default function ViewSubscription({ active }) {
    const classes = useStyles();
    const dispatch = useDispatch();
    useUserRole({ roleList: ['STUDENT'] });
    let profileId = Cookies.get('profileId');
    const { subscriptionData } = useSelector((state) => state?.subscriptions);
    const newData = subscriptionData?.content;

    const handleInvoice = () => {
        dispatch(getInvoice(profileId));
    };

    useEffect(() => {
        dispatch(getProfileSubscription(profileId));
    }, []);

    return (
        <>
            <Grid item container xs={12} sm={12} md={9} lg={9} xl={9} className={classes.card}>
                {/* {active === 'FirstCard' && ( */}
                <>
                    <Card className={classes.infoCard}>
                        <Paper style={{ padding: 20 }}>
                            <div className={classes.titleContainer}>
                                <SchoolIcon className={classes.viewIcon} />
                                <span className={classes.pageTitle}>&nbsp; Subscription Details </span>
                            </div>
                            <Divider style={{ margin: '30px 0px' }} />
                            <Button onClick={handleInvoice}>
                                <GetAppIcon />
                            </Button>
                            {newData?.map((row) => (
                                <Grid container rowSpacing={4}>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Plan Type </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="type" disabled value={row?.type} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Payment Mode </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="paymentMode" disabled value={row?.paymentMode} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Amount </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="amount" disabled value={row?.amount} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Transaction ID </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="transactionId" disabled value={row?.transactionId} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Status </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="status" disabled value={row?.status} fullWidth />
                                        </Grid>
                                    </Grid>

                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Frequency </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="frequency" disabled value={row?.plan?.frequency} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Books Count </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="booksCount" disabled value={row?.plan?.booksCount} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Registration Fee </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="registrationFee" disabled value={row?.plan?.registrationFee} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Security Fee </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="securityFee" disabled value={row?.plan?.securityFee} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Plan option type </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="type" disabled value={row?.plan?.type} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Duration </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="durationInMonths" disabled value={row?.plan?.durationInMonths} fullWidth />
                                        </Grid>
                                    </Grid>
                                    <Grid item container spacing={2}>
                                        <Grid item xs={12} sm={2}>
                                            <label style={{ verticalAlign: 'sub' }}> Max Pause Weeks </label>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <OutlinedInput name="maxPauseWeeks" disabled value={row?.plan?.maxPauseWeeks} fullWidth />
                                        </Grid>
                                    </Grid>
                                </Grid>
                            ))}
                        </Paper>
                    </Card>
                </>
                {/* )} */}
            </Grid>
        </>
    );
}
